/** Media Queries **/
html {
  box-sizing: border-box;
  font-size: 62.5%;
  /*1rem = 10px*/
}

*, *:before, *:after {
  box-sizing: inherit;
}

* {
  font-size: 1.6rem;
}

body.fijar-body {
  overflow: hidden;
}
@media (min-width: 768px) {
  body.fijar-body {
    overflow: auto;
  }
}
body::-webkit-scrollbar {
  background-color: transparent;
  width: 0.5rem;
  margin: 0 10px;
}
body::-webkit-scrollbar-thumb {
  background-color: black;
  border-radius: 0.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 1rem;
}
@media (min-width: 768px) {
  .contenedor {
    margin: 0 auto;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.not-fixed {
  opacity: 0;
  z-index: -2;
}
@media (min-width: 768px) {
  .not-fixed {
    opacity: 1;
    z-index: 0;
  }
}

.header {
  position: fixed;
  width: 100%;
}

.header__contenido {
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  transition: 0.7s;
  padding: 3rem 0;
  align-items: center;
  z-index: 1;
}
@media (min-width: 768px) {
  .header__contenido {
    justify-content: space-between;
  }
}
.header__contenido .header__h1 {
  position: relative;
  display: flex;
  align-items: center;
}
.header__contenido .header__h1 .header__Titulo {
  letter-spacing: 0.5rem;
  font-weight: bold;
  text-decoration: none;
}
.header__contenido .header__h1 .header__Titulo .header__efecto {
  font-size: 1rem;
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-size: 3rem;
  color: rgba(255, 255, 255, 0.946);
  transition: 0.3s ease-in;
}
.header__contenido .header__h1 span {
  display: inline-block;
  color: white;
}
.header__contenido .header__navegacion {
  justify-content: center;
  align-items: center;
  display: none;
  right: 0;
}
@media (min-width: 768px) {
  .header__contenido .header__navegacion {
    display: flex;
  }
}

.header__navegacion ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__navegacion ul li {
  list-style: none;
}
.header__navegacion ul li a {
  position: relative;
  text-decoration: none;
  margin: 0 1.5rem;
  color: #fff;
  letter-spacing: 2px;
  font-weight: lighter;
  transition: 0.7s;
  font-family: "Oswald", sans-serif;
  font-size: 1.6rem;
}
.header__navegacion ul li a:hover {
  color: red;
}

.scroll {
  background-color: rgba(255, 255, 255, 0.748);
  box-shadow: 0px 1px 10px 0px black;
}
.scroll .header__contenido {
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .scroll .header__contenido {
    padding: 2rem 0;
  }
}
.scroll .header__contenido span.one,
.scroll .header__contenido span.two {
  background: #000;
}
.scroll .header__h1 .header__Titulo .header__efecto {
  font-size: 2rem;
  color: black;
}
.scroll .header__h1 span {
  color: black;
}
.scroll .header__navegacion a {
  color: black;
  text-shadow: 1px 1px 1px 1px black;
}

.principal {
  background: url(../img/fondo1.webp);
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center top;
  background-repeat: no-repeat;
}

.main {
  width: 100%;
  height: 100vh;
}

.menu {
  z-index: 0;
  position: fixed;
  background: rgba(255, 255, 255, 0.3);
  /* color de fondo hover */
  backdrop-filter: blur(2px);
  height: 100vh;
  width: 100%;
  top: -100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .menu {
    display: none;
  }
}

.data {
  padding: 8em 0;
  text-align: left;
}
.data ul {
  list-style: none;
  padding: 0;
}
.data ul li {
  color: #fff;
  font-size: 42px;
  text-align: center;
}
.data ul li a {
  text-decoration: none;
  color: #fff;
  font-family: "Oswald", sans-serif;
  font-weight: lighter;
  text-shadow: 1px 0px 10px black;
}

.toggle-btn {
  margin: 1em 1.4em;
  padding: 1em;
  width: 40px;
  height: 12px;
  z-index: 1;
  position: relative;
  cursor: pointer;
}
@media (min-width: 768px) {
  .toggle-btn {
    display: none;
  }
}
.toggle-btn span.one,
.toggle-btn span.two {
  position: absolute;
  width: 40px;
  height: 2px;
  background: #fff;
  left: 50%;
  transform: translate(-50%);
}
.toggle-btn span.two {
  margin-top: 12px;
}

/* OVERLAY
=========================== */
.overlay {
  position: fixed;
  height: 100vh;
  z-index: 99;
  top: 0;
  background: black;
}
.overlay.first {
  width: 100%;
}
.overlay.second {
  left: 33.3%;
  width: 66.6%;
}
.overlay.third {
  left: 66.6%;
  width: 33.3%;
}